<template>
<div style="">
      <el-form v-model='getCarForm'>
      <el-row style='text-align:left'>
        <el-col :span='8'>
          <div style='display:inline-block;'><b>节假日管理:</b></div>
          &nbsp;&nbsp;
          <el-button type="primary" @click="regeist()" style='width:140px;' >新增</el-button>
      </el-col>
      </el-row>
  </el-form>
    <!-- 第二个表格 -->
 <el-table
      :data="tableCar"
      style="width: 100%;padding:10px 20px;margin-top:20px;">
      <el-table-column
      prop="id"
      label="序号"
      width='100'>
    </el-table-column>
      <el-table-column
        prop="holiday"
        label="假期名称">
      </el-table-column>
      <el-table-column
        prop="startTime"
        label="开始时间">
      </el-table-column>
      <el-table-column
        prop="endTime"
        label="结束时间">
      </el-table-column>
      <!-- 操作 -->
      <el-table-column
      label="操作" width="260px">
      <template slot-scope="scope">
          <el-button
              @click="handleChangeMsg(scope.$index, scope.row,scope)"
              type="primary"
              size="small">编辑
          </el-button>
          <el-button
              @click="handleDelMsg(scope.$index, scope.row)"
              type="primary"
              size="small">删除
          </el-button>
      </template>
      </el-table-column>
    </el-table> 
    <!-- 分页条 -->
    <!-- page-sizes, 每页显示条数   
        :page-size 当前页显示条数   feeSystemTradeNo
     -->
    <el-pagination
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="total" v-show='paginationShow' 
        style='margin-top:10px;'
        :page-sizes="[5,10,20,30]"      
        :page-size="pageSize"
        @size-change="handleSizeChange"
        @current-change="currentChange"
        :current-page="currentPage">
    </el-pagination>
    <!-- 模态框 -->
    <el-dialog title="详细信息" 
    :visible.sync="dialogVisible" style='' width='60%'>
    <!-- <div style='height:60vh;position:relative;width:100%;' id='detailBox'> -->
        <el-form v-model='detailForm' disabled>
          <el-row>
            <el-col :span='7'>
              <el-form-item label='车牌号' class='laeblStyle' >
                <el-input v-model='detailForm.plateNo' class='handle-input'></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='7'>
              <el-form-item label='月租类型' class='laeblStyle'>
                <el-input v-model='detailForm.monthlyTypeNo' class='handle-input'></el-input>
              </el-form-item>
            </el-col>
            <div v-show='detailZDY'>
            <el-col :span='7' >
                <el-form-item label='月数(个)' class='laeblStyle'>
                    <el-input v-model='detailForm.months' class='handle-input'>
                    </el-input>
                  </el-form-item>
            </el-col>
          </div>
          </el-row>
          <!-- 第二行 -->
          <el-row>
            <el-col :span='7'>
              <el-form-item label='开始时间' class='laeblStyle' >
                <el-input v-model='detailForm.startTime' class='handle-input'></el-input>
             </el-form-item>
            </el-col>
            <el-col :span='7'>
                <el-form-item label='总租金(元)' class='laeblStyle'>
                    <el-input v-model='detailForm.totalRent' class='handle-input'></el-input>
                  </el-form-item>
            </el-col>
            <el-col :span='8'>
                  <el-form-item label='月租金额(元)' style='width:240px;text-align:right'>
                      <el-input v-model='detailForm.monthlyRent' class='handle-input'></el-input>
                  </el-form-item>
            </el-col>
          </el-row>
          <!-- 车场名称 -->
          <el-row>
            <el-col :span='7'>
              <el-form-item label='到期时间' >
                <el-date-picker
                v-model='detailForm.endTime'
                type="date"  style="width:60%">
                </el-date-picker>
             </el-form-item>
            </el-col>
            <!-- 车牌颜色 -->
            <el-col :span='7'>
                <el-form-item label='车牌颜色'  class='laeblStyle'>
                  <el-input v-model='detailForm.plateColorName' class='handle-input' style=''></el-input>
               </el-form-item>
              </el-col>
              <el-col :span='8'>
                  <el-form-item label='车场名称' >
                    <el-select v-model='detailForm.parkingName' style='width:70%;margin-left:10px;' v-show='parkingIdOptionsShow' clearable>
                        <el-option
                        v-for='item in parkingIdOptions'
                        :key='item.id'
                        :label='item.label'
                        :value='item.value'>
                        </el-option>
                    </el-select>
                 </el-form-item>
                </el-col>
          </el-row>
        </el-form>
      
    <!-- </div> -->
    </el-dialog>
       <!-- 编辑模态框 -->
       <el-dialog title="编辑信息" 
       :visible.sync="dialogVisible2"   width='60%'>
           <el-form v-model='detailForm2'>
            <el-row>
              <el-col :span='7' style=''>
                  <el-form-item label='假期名称' class='laeblStyle'>
                     <el-select v-model='detailForm2.holidayId' class='handle-input' v-show='jqmcShow' clearable>
                         <el-option
                         v-for='item in jqmcOptions'
                         :key='item.id'
                         :label='item.label'
                         :value='item.value'>
                         </el-option>
                     </el-select>
                  </el-form-item>
               </el-col>
               <!-- 第二列 -->
                  <el-col :span='8'>
                      <el-form-item label='开始时间'>
                          <el-date-picker
                          v-model="detailForm2.startTime"
                          type="datetime"
                          placeholder="选择日期时间" style='width:70%'>
                        </el-date-picker>
                      </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                  </el-col>
                  <el-col :span='8'>
                      <el-form-item label='结束时间'>
                          <el-date-picker
                          v-model="detailForm2.endTime"
                          type="datetime"
                          placeholder="选择日期时间" style='width:70%'>
                        </el-date-picker>
                      </el-form-item>
                  </el-col>
          </el-row>

             <!-- 按钮行 -->
						<div style='float:right;margin:10px 15% 0 0;'>
								<el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel()'>取消</el-button>
							<el-button class="el-button el-button--primary" style='width:80px' @click='changeMonthly(detailForm2)'>保存</el-button>
						</div>
            <div style="clear:both"></div>
         
   
           </el-form>
       </el-dialog>
       <!-- 新增月租车 -->
       <el-dialog title="新增假期" 
       :visible.sync="yzcDialog" width='65%'>
        <el-form v-model='monthCarForm'>
          <el-row>
              <el-col :span='7' style=''>
                  <el-form-item label='假期名称' class='laeblStyle'>
                     <el-select v-model='monthCarForm.jqmc' class='handle-input' v-show='jqmcShow' clearable>
                         <el-option
                         v-for='item in jqmcOptions'
                         :key='item.id'
                         :label='item.label'
                         :value='item.value'>
                         </el-option>
                     </el-select>
                  </el-form-item>
               </el-col>
               <!-- 第二列 -->
                  <el-col :span='8'>
                      <el-form-item label='开始时间'>
                          <el-date-picker
                          v-model="monthCarForm.startTime"
                          type="datetime"
                          placeholder="选择日期时间">
                        </el-date-picker>
                      </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                  </el-col>
                  <el-col :span='8'>
                      <el-form-item label='结束时间'>
                          <el-date-picker
                          v-model="monthCarForm.endTime"
                          type="datetime"
                          placeholder="选择日期时间">
                        </el-date-picker>
                      </el-form-item>
                  </el-col>
          </el-row>
          <!-- 按钮行 -->
          <div style="float:right;margin-right:10%;">
            <el-button type='button' class='el-button el-button--warning' style="margin-right:20px;" @click='cancelDialog()'>取消</el-button>
            <el-button type='button' class='el-button el-button--primary' style="" @click='regeistMsg(monthCarForm)'>保存</el-button>
           </div>
           <div style='clear:both'></div>
       


          
        </el-form>
      </el-dialog>






</div>
</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  export default {
    methods: {
      //假期管理
      getHoilday(){
        var self=this;
        var url=self.baseUrl+"holiday/init";
        self.$axios.get(url).then(function(res){
          console.log("假期列表")
          console.log(res.data)
          self.jqmcShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.jqmcOptions[i]={
              id:res.data.data[i].holidayId,
              value:res.data.data[i].holidayId,
              label:res.data.data[i].holiday,
            }
          }
          console.log("假期")
          console.log(self.jqmcOptions)
        }).catch(err=>console.log(err))
      },
      //编辑信息
      handeChangeYZ(val){
        console.log(val)
        if(val=='5'){
          this.editYZ=true
        }else{
          this.editYZ=false
        }
      },
      //取消对话框
      cancelDialog(){
        this.yzcDialog=false;
      },
      regeistMsg(val){
        var self=this;
        var params={};
        var url=self.baseUrl+"holiday/add";
        var params={
          "holidayId":self.monthCarForm.jqmc,
          "startTime":self.momentTime(self.monthCarForm.startTime),
          "endTime":self.momentTime(self.monthCarForm.endTime)
        }
        self.$axios.post(url,params).then(function(res){
          console.log(res.data);
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.yzcDialog=false;
              //刷新
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(err=>{console.log(err)})
      
      },
      yzVal(val){
        console.log(val);
        var self=this;
        if(val=='5'){
          self.zdyYzc=true;
        }else{
          self.zdyYzc=false;
        }
      },
      //月租类型
      getMonthType(){
        var self=this;
        var url=self.baseUrl+"vehicleMonthly/initMonthly";
        self.$axios.post(url).then(function(res){
          console.log(res.data.data)
          self.monthlyTypeShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.monthlyTypeOptions[i]={
              id:res.data.data[i].monthlyTypeNo,
							label:res.data.data[i].monthlyType,
							value:res.data.data[i].monthlyTypeNo,
            }
          }
        })

      },
      	// 车牌颜色
		getCarColor(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/plateColorList"
			self.$axios.get(url).then(function(res){
				self.plateColorOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].plateColorNo,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
      //注册
      regeist(){
        var self=this;
        self.yzcDialog=true;
      },
      //删除月租车
      handleDelMsg(index,row){
        console.log(row)
        var self=this;
        var url=self.baseUrl+"holiday/delete?id="+row.id;
        //确认。。
        this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          self.$axios.get(url).then(function(res){
          console.log(res.data.message)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.mesage,
              type:"info"
            })
          }
        }).catch(err=>console.log(err))

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });


      },
      //修改月租
      changeMonthly(val){
        console.log('看下数据9999')
        var self=this;
        var url=self.baseUrl+"holiday/update";
        var params={
          "id":self.getRowId,
          "holidayId":val.holidayId,
          "startTime":self.momentTime(val.startTime),
          "endTime":self.momentTime(val.endTime)
        }
        self.$axios.post(url,params).then(function(res){
          console.log("修改的返回值")
          console.log(res.data)
        if(res.data.status=='0'){
          self.$message({
            message:res.data.message,
            type:"success"
          })
          self.dialogVisible2=false;
        }else{
          self.$message({
            message:res.data.message,
            type:"info"
          })
        }
        }).catch(err=>console.log(err))
      },
      //月租类型
      getMonthly(){
        var self=this;
        var url=self.baseUrl+'vehicleMonthly/initMonthly';
        self.$axios.get(url).then(function(res){
          console.log("月租类型res")
          console.log(res.data)
          for(var i=0;i<res.data.data.length;i++){
            self.monthlyTypeNoOptions[i]={
              value:res.data.data[i].monthlyTypeNo,
              label:res.data.data[i].monthlyType
            }
          }
        }).catch(err=>console.log(err))
      },
      cancel(){
        this.dialogVisible2=false;
      },
      getParkingId(){
        var self=this;
			var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId");
			self.$axios.get(url).then(function(res){
				self.parkingIdOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
      },
      axiosChangeCar(params){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/updateVehicle";
        self.$axios.post(url,params).then(function(res){
          console.log('res.data.......');
          console.log(res.data);
        }).catch(function(err){
          console.log(err);
        })              
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url=self.baseUrl +"vehicleMonthly/list"
            var paramString={
              'plateNo':this.inputCph,
              'currentPage':this.currentPage,
              'pageSize':this.pageSize,
              "parkingId":this.getCarForm.parkingId

            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(url,jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          var url=self.baseUrl +"vehicleMonthly/list"
          var str={
            'plateNo':this.inputCph,
              'currentPage':1,
              'pageSize':this.pageSize,
              "parkingId":this.getCarForm.parkingId
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(url,jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(url,params){
        var self=this;
        // this.paginationShow=true;
        this.$axios({
          method:"get",
          url:url,
          headers:{
            "Content-Type":"application/json"
          },
          // data:params
        }).then(function(response){
          self.tableCar=response.data.data;
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime1(val){
          return moment(val).format("YYYY-MM-DD");
      },
      getCarMsg(){
        var self=this;    
          var url=self.baseUrl +"holiday/list"
          //查询，获取参数
          var str={
            // 'parkingId':self.getCarForm.parkingId,
            // "plateNo":self.getCarForm.plateNo,
            // "currentPage":1,
            // "pageSize":10
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(url,jsonParams);
      },
      handleDetailMsg(index,row,scope){
        var self=this;
        this.dialogVisible=true;
        //调用回显接
        var url=self.baseUrl+"vehicleMonthly/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          console.log("回显接口自定义")
          console.log(res.data.data)
          if(res.data.status=='0'){
            if(res.data.data.monthlyType=='自定义'){
              self.detailZDY=true;
            }else{
              self.detailZDY=false
            }
            self.detailForm=res.data.data;
            self.detailForm.monthlyTypeNo=res.data.data.monthlyType;
            self.detailForm.startTime=self.momentTime1(self.detailForm.startTime)
          }else{
            sele.$message({
              message:"请求失败,请重试!",
              type:"info"
            })
          }
        }).catch(err=>console.info(err));
      },
      handleChangeMsg(index,row,scope){
        var self=this;
        self.dialogVisible2=true;
        console.log("编辑的row")
        self.getRowId=row.id//缓存id
        //调用回显-显示
        var url=self.baseUrl+"holiday/detail?id="+row.id;
        self.$axios.get(url).then(function(res){
          console.log("假期 回显接口")
          console.log(res.data.data)
          self.detailForm2=res.data.data;
        }).catch(err=>console.info(err));
      }
    },
    data() {
      return {
        getRowId:'',
        jqmcOptions:[],
        jqmcShow:false,
        detailZDY:false,
        editYZ:false,
        zdyYzc:false,
        monthlyTypeShow:false,
        monthlyTypeOptions:[],        
        yzcDialog:false,
        monthCarForm:{},
        getCarForm:{},
          inputCph:'',
          paginationShow:false,
          tableShow:[],
          tableCar:[],
          total:0,
          currentPage:2,
          pageSize:10,
          dialogVisible:false,
          row:[],
          imgRul2:'',
          zfVal:'',
          zfOptions:[
            {id:1,label:"已支付",value:"true"},
            {id:2,label:"未支付",value:"false"}
          ],
          tiemVal:'',
          detailForm:{},
          detailType:false,
          detailType2:false,
          dialogVisible2:false,
          detailForm2:{},
          parkingIdOptionsShow:false,
          parkingIdOptions:[],
          vehicleTypeOptionsShow:false,
          vehicleTypeOptions:[],
          plateColorOptions:[],
          plateColorOptionsShow:false,
          isReletOptions:[
            {id:0,value:0,label:"否"},
            {id:1,value:1,label:"是"},
          ],
          monthlyTypeNoOptions:[]
      }
    },
    mounted:function(){
        this.getParkingId();
        this.getMonthly();
        this.getCarMsg();
        this.getCarColor();
        this.getMonthType();
        this.getHoilday();
    }
  }
</script>
<style scoped>
    .sjInput{
    width:150px;
  }
.customWidth{
  /* width:10%; */
}
.fl{
  float:left;
}.fr{
  float:right;
}
.bd{
  height:14%;
  font-size:0.8em;
  line-height:1em;
  margin-bottom:10px;
}
.marL{
  margin-right:20%;
}
.width{
  width:30%
}
.width2{
  width:30%
}
.textLeft{
  text-align: left;
}
.handle-input{
  width:140px;
  display: inline-block;
}
.laeblStyle{
  width:230px;
  text-align:right
}
</style>


